body {
    background-color: #113e6b;
    margin: 0;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: '16px';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.home, .service {
    margin: 8% 0 10% 15%;
    text-wrap: normal;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}

a {
    color: white;
}
[class*="col-"] {
    float: left;
}

.row::after {
    content: "";
    clear: both;
    display: table;
}

.col-1 {
    width: 8.33%;
}
.col-2 {
    width: 16.66%;
}
.col-3 {
    width: 25%;
}
.col-4 {
    width: 33.33%;
}
.col-5 {
    width: 41.66%;
}
.col-6 {
    width: 50%;
}
.col-7 {
    width: 58.33%;
}
.col-8 {
    width: 66.66%;
}
.col-9 {
    width: 75%;
}
.col-10 {
    width: 83.33%;
}
.col-11 {
    width: 91.66%;
}
.col-12 {
    width: 100%;
}

@media only screen and (max-width: 700px) {
    .col--s1 {
        width: 8.33%;
    }

    .col-s2 {
        width: 16.66%;
    }

    .col-s3 {
        width: 25%;
    }

    .col-s4 {
        width: 33.33%;
    }

    .col-s5 {
        width: 41.66%;
    }

    .col-s6 {
        width: 50%;
    }

    .col-s7 {
        width: 58.33%;
    }

    .col-s8 {
        width: 66.66%;
    }

    .col-s9 {
        width: 75%;
    }

    .col-s10 {
        width: 83.33%;
    }

    .col-s11 {
        width: 91.66%;
    }

    .col-s12 {
        width: 100%;
    }
}